* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.Grabmart {
  display: flex;
  justify-content: center;
  position: relative;
}

.Grabmart i {
  position: absolute;
  right: 110px;
  top: 25%;
  transform: translateY(-40%); /* To truly center item */
  font-size: 45px;
  color: white;
}

.Grabmart h1 {
  color: white;
  position: relative;
  right: 10px;
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 20px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-image: url("../Photos/LoginBg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.LoginPage {
  width: 520px;
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  color: black;
  border-radius: 10px;
  padding: 30px 40px;
}

.LoginPage h1 {
  font-size: 36px;
  text-align: center;
  color: black;
}

.LoginPage .input-box {
  position: relative;
  width: 100%;
  height: 50px;
  margin: 30px 0;
}

.input-box input {
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
  border: 2px solid black;
  border-radius: 40px;
  font-size: 16px;
  color: black;
  padding: 20px 45px 20px 20px;
}

.input-box input::placeholder {
  color: black;
}

.input-box i {
  position: absolute; /* Position is relative of inputbox, absolute means relative to the input box it will place itself*/
  right: 20px;
  top: 50%;
  transform: translateY(-50%); /* To truly center item */
  font-size: 20px;
}

.LoginPage .remember-forgot {
  display: flex;
  justify-content: space-between;
  font-size: 14.5px;
  margin: -15px 0px 15px;
}

.remember-forgot label input {
  accent-color: black;
  margin-right: 3px;
}

.remember-forgot a {
  color: white;
  text-decoration: none;
}

.remember-forgot a:hover {
  text-decoration: underline;
}

.LoginPage .button {
  width: 100%;
  height: 45px;
  background-color: black;
  border: none;
  outline: none;
  border-radius: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  font-size: 16px;
  color: white;
  font-weight: 600;
}

.LoginPage .register-link {
  font-size: 14.5px;
  text-align: center;
  margin: 20px 0px 15px;
}

.register-link p a {
  color: black;
  text-decoration: none;
  font-weight: 600;
}

.register-link p a:hover {
  text-decoration: underline;
}
